[rem="1920"] {
  font-size: 5.2083333333vw;
}

[rem="750"] {
  font-size: 13.333333333vw;
}

[rem="390"] {
  font-size: 25.641025641vw;
}

* {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  font-size: 16px;
}

figure,
p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

dd,
dl,
dt,
li,
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  background-color: transparent;
  text-decoration: none;
}

img {
  border: 0;
  max-width: 100%;
  max-height: 100%;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

button,
input,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

textarea {
  overflow: auto;
}

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}

template {
  display: none;
}

.hidden2,
.hidden3,
.hidden4,
.hidden5 {
  display: -webkit-box; /* autoprefixer:off */
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.hidden1 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.hidden2 {
  -webkit-line-clamp: 2;
}

.hidden3 {
  -webkit-line-clamp: 3;
}

.hidden4 {
  -webkit-line-clamp: 4;
}

.hidden5 {
  -webkit-line-clamp: 5;
}

.clearfix::after,
.clearfix::before {
  content: "";
  width: 0;
  height: 0;
  display: block;
  clear: both;
}
